import React, { useState } from 'react';
import { Layout, Card, Typography, Avatar, Button, Dropdown, Space } from 'antd';
import { UserOutlined, RobotOutlined, SendOutlined, ExportOutlined, DownloadOutlined, FileTextOutlined, FileOutlined, FilePdfOutlined } from '@ant-design/icons';
import ChatInput from './ChatInput';

const { Content } = Layout;
const { Title, Text } = Typography;

interface Message {
  id: string;
  content: string;
  sender: 'user' | 'bot';
  timestamp: Date;
  hasResumeDownload?: boolean;
}

interface UserProfile {
  name?: string;
  currentRole?: string;
  experience?: string;
  skills?: string[];
  interests?: string[];
  goals?: string[];
  challenges?: string[];
}

interface AppState {
  isRedirecting: boolean;
  pendingRedirectUrl?: string;
  awaitingUserConfirmation?: boolean;
  generatedResume?: string;
  showResumeDownload?: boolean;
  lastBotResponseType?: 'career_guidance' | 'resume_request' | 'general';
}

const CareerGuideChat: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      content: ' **AI**\n\n\n\n **** - \n **** - AI\n **** - \n **** - \n **** - \n\n****\n \n \n \n Open Resume\n \n\n****\n\n\n  |   |   |   |  ',
      sender: 'bot',
      timestamp: new Date()
    }
  ]);
  
  const [userProfile, setUserProfile] = useState<UserProfile>({});
  const [conversationContext, setConversationContext] = useState<string[]>([]);
  const [appState, setAppState] = useState<AppState>({
    isRedirecting: false,
    awaitingUserConfirmation: false,
    showResumeDownload: false
  });

  const handleSendMessage = (content: string) => {
    const userMessage: Message = {
      id: Date.now().toString(),
      content,
      sender: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setConversationContext(prev => [...prev, content]);

    // 
    updateUserProfile(content);

    // Generate bot response
    setTimeout(() => {
      const botResponse = generateBotResponse(content);
      const isResumeGenerated = botResponse.includes(' ****');
      
      const botMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: botResponse,
        sender: 'bot',
        timestamp: new Date(),
        hasResumeDownload: isResumeGenerated
      };
      setMessages(prev => [...prev, botMessage]);
    }, 1000);
  };

  const updateUserProfile = (userInput: string) => {
    const input = userInput.toLowerCase();
    const newProfile = { ...userProfile };

    // 
    if (input.includes('') || input.includes('') || input.includes('')) {
      newProfile.currentRole = '';
    } else if (input.includes('') || input.includes('ui') || input.includes('ux')) {
      newProfile.currentRole = '';
    } else if (input.includes('') || input.includes('pm')) {
      newProfile.currentRole = '';
    } else if (input.includes('') || input.includes('')) {
      newProfile.currentRole = '/';
    }

    // 
    if (input.includes('') || input.includes('') || input.includes('0') || input.includes('')) {
      newProfile.experience = '';
    } else if (input.includes('1') || input.includes('2') || input.includes('')) {
      newProfile.experience = '';
    } else if (input.includes('3') || input.includes('4') || input.includes('5') || input.includes('')) {
      newProfile.experience = '';
    } else if (input.includes('') || input.includes('') || input.includes('')) {
      newProfile.experience = '';
    }

    // 
    const skills = [];
    if (input.includes('javascript') || input.includes('js')) skills.push('JavaScript');
    if (input.includes('python')) skills.push('Python');
    if (input.includes('java')) skills.push('Java');
    if (input.includes('react')) skills.push('React');
    if (input.includes('vue')) skills.push('Vue');
    if (input.includes('node')) skills.push('Node.js');
    if (input.includes('ai') || input.includes('') || input.includes('')) skills.push('AI/ML');
    if (skills.length > 0) {
      newProfile.skills = [...(newProfile.skills || []), ...skills];
    }

    // 
    const goals = [];
    if (input.includes('') || input.includes('')) goals.push('');
    if (input.includes('') || input.includes('')) goals.push('');
    if (input.includes('') || input.includes('')) goals.push('');
    if (input.includes('')) goals.push('');
    if (goals.length > 0) {
      newProfile.goals = [...(newProfile.goals || []), ...goals];
    }

    setUserProfile(newProfile);
    return newProfile;
  };

  const parsePersonalInfo = (input: string): any => {
    const newProfile: any = { ...userProfile };

    const nameMatch = input.match(/(?:|||)\s*([^,\s]+)/);
    if (nameMatch) newProfile.name = nameMatch[1];

    const positionMatch = input.match(/(?:|||)\s*([^,\s]+)/);
    if (positionMatch) newProfile.targetPosition = positionMatch[1];

    const salaryMatch = input.match(/(?:||)\s*([^,\s]+)/);
    if (salaryMatch) newProfile.expectedSalary = salaryMatch[1];

    const cityMatch = input.match(/(?:||)\s*([^,\s]+)/);
    if (cityMatch) newProfile.targetCities = cityMatch[1];

    const skillsMatch = input.match(/(?:|||)\s*([^]+)/);
    if (skillsMatch) {
      const skills = skillsMatch[1].toLowerCase();
      if (skills.includes('javascript') || skills.includes('js')) newProfile.jsLevel = '';
      if (skills.includes('react') || skills.includes('vue')) newProfile.frameworkLevel = '';
      if (skills.includes('css') || skills.includes('')) newProfile.cssLevel = '';
    }

    const experienceMatch = input.match(/(?:|)\s*([^,\s]+)/);
    if (experienceMatch) newProfile.experience = experienceMatch[1];

    const educationMatch = input.match(/(?:|)\s*([^,\s]+)/);
    if (educationMatch) newProfile.education = educationMatch[1];

    setUserProfile(newProfile);
    return newProfile;
  };

  const createResumeContent = (info: any): string => {
    return `# **${info.name || ''}**
**${info.targetPosition || 'Web'}**

 ${info.email || 'your-email@example.com'}  
 ${info.phone || '138-xxxx-xxxx'}  
 GitHubgithub.com/username  
 ${info.targetCities || ''}

---

## ** **
- ****${info.targetPosition || 'Web'}
- ****${info.expectedSalary || ''}
- ****
- ****

---

## ** **

****
- JavaScript${info.jsLevel || ''} - 
- React/Vue${info.frameworkLevel || ''} - 
- CSS${info.cssLevel || ''} - 
- HTML - 

****
- WebpackVite
- Git - 

****
- HTTP/HTTPSTCP/IP
- 

---

## ** **

### ****
**

****

****

****

****

---

## ** **

**${info.education || 'XX |  |  | 2020-2024'}**

---

## ** **

- ****JavaScript
- ****
- ****
- ****
    `;
  };

  const generateResumeTemplate = (info: any): string => {
    const resumeContent = createResumeContent(info);
    setAppState(prev => ({ ...prev, generatedResume: resumeContent, awaitingUserConfirmation: false }));
    return ` ****

 ****

${resumeContent}

---

** **

`;
  };

  const detectPersonalProfile = (input: string): boolean => {
    const indicators = [
      '', '', 'react', 'vue', 'js', 'javascript',
      '', '', '', '', '',
      '', '', '', '', 'css', 'webpack',
      'vite', '', '', '', '', ''
    ];
    const lowerInput = input.toLowerCase();
    const matchCount = indicators.filter(indicator => lowerInput.includes(indicator)).length;
    return matchCount >= 4 && input.length > 20;
  };

  const handleRedirectResponse = (input: string): string => {
    const normalizedInput = input.toLowerCase().trim();

    if (detectPersonalProfile(input)) {
      const newProfile = parsePersonalInfo(input);
      return generateResumeTemplate(newProfile);
    }

    if (normalizedInput.includes('') || normalizedInput.includes('') || normalizedInput.includes('yes') || normalizedInput.includes('ok') || normalizedInput.includes('') || normalizedInput.includes('')) {
      setAppState(prev => ({ ...prev, awaitingUserConfirmation: false, isRedirecting: true }));
      setTimeout(() => {
        window.open('https://www.open-resume.com/', '_blank');
        setAppState(prev => ({ ...prev, isRedirecting: false }));
      }, 2000);
      return ` Open Resume...`;
    } else if (normalizedInput.includes('') || normalizedInput.includes('') || normalizedInput.includes('no') || normalizedInput.includes('')) {
      setAppState(prev => ({ ...prev, awaitingUserConfirmation: false }));
      return ` ****



****
"25kReactVueJS3XX"`;
    } else {
      return `

  **""**  **""** -  Open Resume
  **""**  **""** - 
 `;
    }
  };

  const generateResumeRedirectConfirmation = (): string => {
    setAppState(prev => ({ ...prev, awaitingUserConfirmation: true }));
    return ` ****

 ** Open Resume **
  **** - 
  **** - AI
  **ATS** - 
  **** - 
  **** - 

 ** Open Resume **

 **""**  **""** -  Open Resume
 **""**  **""** - 

`;
  };

  const generateBotResponse = (userInput: string): string => {
    const input = userInput.toLowerCase();

    if (appState.awaitingUserConfirmation) {
      return handleRedirectResponse(input);
    }

    // Check for explicit resume requests first
    if (input.includes('') || input.includes('resume') || input.includes('cv')) {
      setAppState(prev => ({ ...prev, lastBotResponseType: 'resume_request' }));
      return generateResumeRedirectConfirmation();
    }

    // Career guidance responses - set context
    if (input.includes('')) {
      setAppState(prev => ({ ...prev, lastBotResponseType: 'career_guidance' }));
      return generateGraduateAdvice();
    }
    if (input.includes('')) {
      setAppState(prev => ({ ...prev, lastBotResponseType: 'career_guidance' }));
      return generateCareerChangeAdvice();
    }
    if (input.includes('')) {
      setAppState(prev => ({ ...prev, lastBotResponseType: 'career_guidance' }));
      return generateSkillAdvice();
    }
    if (input.includes('')) {
      setAppState(prev => ({ ...prev, lastBotResponseType: 'career_guidance' }));
      return generateJobPrepAdvice();
    }
    if (input.includes('') || input.includes('ai')) {
      setAppState(prev => ({ ...prev, lastBotResponseType: 'career_guidance' }));
      return generateAIAssistAdvice();
    }

    // Only generate resume if NOT responding to career guidance AND profile is detected
    if (appState.lastBotResponseType !== 'career_guidance' && detectPersonalProfile(userInput)) {
      const newProfile = parsePersonalInfo(userInput);
      return generateResumeTemplate(newProfile);
    }

    // If responding to career guidance with personal info, provide targeted advice
    if (appState.lastBotResponseType === 'career_guidance' && detectPersonalProfile(userInput)) {
      return generatePersonalizedCareerAdvice(userInput);
    }

    setAppState(prev => ({ ...prev, lastBotResponseType: 'general' }));
    return generateSimpleResponse(input);
  };

  const generateGraduateAdvice = (): string => {
    return ` ****

 ****
 
 
 
 

 **6**

**1-2** 
 React/VueSpring/Django
 3-5
 Git

**3-4** 
 1
 
 

**5-6** 
 LeetCode 200+
 
 

 ****
 ChatGPT
 GitHub Copilot
 Claude

`;
  };

  const generateAIAssistAdvice = (): string => {
    return ` ****

 ****

**1. ** 
 AI
 
 

**2. ** 
 GitHub Copilot
 ChatGPT
 Claude

**3. ** 
 AI
 
 

 ****

****
 GitHub Copilot - 
 Cursor - AIIDE
 Tabnine - 

****
 ChatGPT - 
 Claude - 
 Perplexity - 

****
 AI
 
 

 ****

**** 
 
 
 

**** 
 AI
 
 

`;
  };

  const generatePersonalizedCareerAdvice = (userInput: string): string => {
    const input = userInput.toLowerCase();
    
    // 
    const profile = parsePersonalInfo(userInput);
    
    return ` ****

**1. ** 
 ${profile.skills?.includes('React') || profile.skills?.includes('Vue') ? 'React/VueJS ' : 'React/VueJS'}
 ${input.includes('webpack') || input.includes('vite') ? 'WebpackVite ' : 'WebpackVite '}
 ${input.includes('') || input.includes('') ? ' ' : ' '}
 ${input.includes('') ? '' : ''}

**2. ** 
 ${profile.targetPosition || 'Web'}
 ${profile.expectedSalary || '20k'}
 ${profile.targetCities || ''}

**3. ** 
**3-6**
 LeetCode200+
 Vue3/React18
 1
 

**6-12**
 
 
 

**4. ** 
 LeetCode + Offer
 Vue3/React + 
 Webpack/Vite
 GitHub

`;
  };

  const generateSimpleResponse = (input: string): string => {
    if (input.includes('')) {
      return ` ****


 React/Vue
   
 
 

`;
    }

    return ` ****

 
 
 

`;
  };

  const generateJobPrepAdvice = (): string => {
    return ` ****

 ****
 
 3-5
 
 

 **3**

**1** 
 
 
 2-3

**2** 
 STAR
 
 

**3** 
 
 
 

 **AI**
 ChatGPT
 AI
 AI

`;
  };

  const generateSkillAdvice = (): string => {
    return ` ****

****
 2-3
 
 
 

****
 AWS/Azure/
 Docker/K8s
 AI/
 DevOps

****
 
 
 
 

****
  + 
 
 
 

`;
  };

  const generateCareerChangeAdvice = (): string => {
    return ` ****

****
1. ****
2. ****
3. ****
4. ****

****
 ****
 ****
 ****
 ****

****
 3-6
 6-12
 1-2

****
 
 
 

 ****
 
 
 
 `;
  };

  const downloadResumeAsHTML = () => {
    if (!appState.generatedResume) return;
    
    const htmlContent = `<!DOCTYPE html>
<html lang="zh-CN">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title></title>
    <style>
        body {
            font-family: 'Microsoft YaHei', Arial, sans-serif;
            line-height: 1.6;
            color: #333;
            max-width: 800px;
            margin: 0 auto;
            padding: 20px;
            background-color: #fff;
        }
        h1 {
            color: #2c3e50;
            border-bottom: 3px solid #3498db;
            padding-bottom: 10px;
            margin-bottom: 20px;
        }
        h2 {
            color: #34495e;
            border-left: 4px solid #3498db;
            padding-left: 15px;
            margin-top: 30px;
            margin-bottom: 15px;
        }
        h3 {
            color: #2c3e50;
            margin-top: 20px;
        }
        .contact-info {
            background-color: #f8f9fa;
            padding: 15px;
            border-radius: 8px;
            margin-bottom: 20px;
        }
        .skills-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
            gap: 15px;
            margin: 15px 0;
        }
        .skill-category {
            background-color: #f1f2f6;
            padding: 10px;
            border-radius: 5px;
        }
        .project {
            border-left: 3px solid #e74c3c;
            padding-left: 15px;
            margin: 20px 0;
        }
        .project-time {
            color: #7f8c8d;
            font-style: italic;
        }
        ul {
            padding-left: 20px;
        }
        li {
            margin-bottom: 5px;
        }
        @media print {
            body { margin: 0; padding: 15px; }
            .no-print { display: none; }
        }
    </style>
</head>
<body>
    ${appState.generatedResume.replace(/\n/g, '<br>').replace(/#{1,3}\s*\*\*(.*?)\*\*/g, '<h2>$1</h2>').replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>').replace(/---/g, '<hr>')}
</body>
</html>`;
    
    const blob = new Blob([htmlContent], { type: 'text/html;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `_${new Date().toISOString().slice(0, 10)}.html`;
    link.click();
    URL.revokeObjectURL(url);
  };

  const downloadResumeAsText = () => {
    if (!appState.generatedResume) return;
    
    const blob = new Blob([appState.generatedResume], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `_${new Date().toISOString().slice(0, 10)}.txt`;
    link.click();
    URL.revokeObjectURL(url);
  };

  const exportAsText = () => {
    const timestamp = new Date().toLocaleString('zh-CN');
    let content = `AI\n: ${timestamp}\n${'='.repeat(50)}\n\n`;
    
    messages.forEach((message, index) => {
      const time = message.timestamp.toLocaleTimeString();
      const sender = message.sender === 'user' ? '' : 'AI';
      content += `[${time}] ${sender}:\n${message.content}\n\n`;
    });
    
    const blob = new Blob([content], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `_${new Date().toISOString().slice(0, 10)}.txt`;
    link.click();
    URL.revokeObjectURL(url);
  };

  const exportAsJSON = () => {
    const exportData = {
      exportTime: new Date().toISOString(),
      conversationCount: messages.length,
      userProfile,
      messages: messages.map(msg => ({
        id: msg.id,
        content: msg.content,
        sender: msg.sender,
        timestamp: msg.timestamp.toISOString()
      }))
    };
    
    const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `_${new Date().toISOString().slice(0, 10)}.json`;
    link.click();
    URL.revokeObjectURL(url);
  };

  const exportMenuItems = [
    {
      key: 'text',
      label: '',
      icon: <FileTextOutlined />,
      onClick: exportAsText
    },
    {
      key: 'json', 
      label: 'JSON',
      icon: <FileOutlined />,
      onClick: exportAsJSON
    }
  ];

  const resumeDownloadItems = [
    {
      key: 'html',
      label: 'HTML',
      icon: <FilePdfOutlined />,
      onClick: downloadResumeAsHTML
    },
    {
      key: 'text',
      label: '',
      icon: <FileTextOutlined />,
      onClick: downloadResumeAsText
    }
  ];

  return (
    <Layout style={{ height: '100vh', backgroundColor: '#f0f2f5' }}>
      <Content style={{ padding: '24px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        <div style={{ width: '100%', maxWidth: '800px', height: '100%', display: 'flex', flexDirection: 'column' }}>
          <Card 
            className="chat-window-header"
            style={{ 
              marginBottom: '16px', 
              borderRadius: '12px',
              boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
            }}
          >
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <Title level={4} style={{ color: 'white', margin: 0, fontWeight: 'bold' }}>
                AI 
              </Title>
              <Space>
                {appState.showResumeDownload && (
                  <Dropdown menu={{ items: resumeDownloadItems }} placement="bottomRight">
                    <Button type="primary" icon={<DownloadOutlined />} size="small">
                      
                    </Button>
                  </Dropdown>
                )}
                {messages.length > 1 && (
                  <Dropdown menu={{ items: exportMenuItems }} placement="bottomRight">
                    <Button icon={<DownloadOutlined />} size="small">
                      
                    </Button>
                  </Dropdown>
                )}
              </Space>
            </div>
          </Card>

          <Card 
            className="chat-window"
            style={{ flex: 1, display: 'flex', flexDirection: 'column', borderRadius: '12px', boxShadow: '0 4px 12px rgba(0,0,0,0.1)' }}
            styles={{ body: { padding: '12px 16px', flex: 1, overflowY: 'auto' } }}
          >
            {messages.map((message) => (
              <Card
                key={message.id}
                style={{
                  marginBottom: '15px',
                  borderRadius: '12px',
                  boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                }}
                styles={{ body: { padding: '16px' } }}
              >
                <div style={{ 
                  display: 'flex', 
                  flexDirection: message.sender === 'user' ? 'row-reverse' : 'row',
                  gap: '12px',
                  width: '100%'
                }}>
                  <Avatar 
                    icon={message.sender === 'user' ? <UserOutlined /> : <RobotOutlined />}
                    style={{ 
                      backgroundColor: message.sender === 'user' ? '#52c41a' : '#1890ff',
                      flexShrink: 0,
                      boxShadow: '0 2px 8px rgba(0,0,0,0.15)'
                    }}
                    size={40}
                  />
                  <div style={{ 
                    flex: 1,
                    maxWidth: message.sender === 'user' ? '70%' : '85%'
                  }}>
                    <div style={{
                      backgroundColor: message.sender === 'user' ? '#f0f9ff' : '#ffffff',
                      border: `1px solid ${message.sender === 'user' ? '#91d5ff' : '#e6f7ff'}`,
                      borderRadius: '12px',
                      padding: '16px',
                      position: 'relative',
                      boxShadow: '0 1px 4px rgba(0,0,0,0.08)'
                    }}>
                      <div style={{
                        position: 'absolute',
                        top: '16px',
                        [message.sender === 'user' ? 'right' : 'left']: '-8px',
                        width: '16px',
                        height: '16px',
                        backgroundColor: message.sender === 'user' ? '#f0f9ff' : '#ffffff',
                        border: `1px solid ${message.sender === 'user' ? '#91d5ff' : '#e6f7ff'}`,
                        borderRadius: '2px',
                        transform: 'rotate(45deg)',
                        borderTop: 'none',
                        borderLeft: 'none'
                      }} />
                      
                      <Text strong style={{ 
                        color: message.sender === 'user' ? '#52c41a' : '#1890ff',
                        fontSize: '14px',
                        marginBottom: '8px',
                        display: 'block'
                      }}>
                        {message.sender === 'user' ? '' : ' AI'}
                      </Text>
                      
                      <div style={{ 
                        whiteSpace: 'pre-line',
                        lineHeight: '1.6',
                        fontSize: '14px',
                        color: '#262626'
                      }}>
                        <Text>{message.content}</Text>
                      </div>
                      
                      {/* Resume Download Button - Only show on messages with resume */}
                      {message.sender === 'bot' && message.hasResumeDownload && appState.generatedResume && (
                        <div style={{ marginTop: '16px' }}>
                          <Dropdown
                            menu={{
                              items: [
                                {
                                  key: 'html',
                                  label: 'HTML ()',
                                  icon: <DownloadOutlined />,
                                  onClick: downloadResumeAsHTML
                                },
                                {
                                  key: 'text',
                                  label: '',
                                  icon: <DownloadOutlined />,
                                  onClick: downloadResumeAsText
                                }
                              ]
                            }}
                            placement="topLeft"
                          >
                            <Button 
                              type="primary" 
                              icon={<DownloadOutlined />}
                              style={{
                                background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                                border: 'none',
                                borderRadius: '8px',
                                boxShadow: '0 4px 12px rgba(102, 126, 234, 0.4)'
                              }}
                            >
                               
                            </Button>
                          </Dropdown>
                        </div>
                      )}
                      
                      <div style={{ 
                        marginTop: '12px',
                        textAlign: message.sender === 'user' ? 'right' : 'left'
                      }}>
                        <Text type="secondary" style={{ fontSize: '11px' }}>
                          {message.timestamp.toLocaleTimeString()}
                        </Text>
                      </div>
                    </div>
                  </div>
                </div>
              </Card>
            ))}
          </Card>
          <ChatInput onSend={handleSendMessage} />
        </div>
      </Content>
    </Layout>
  );
};

export default CareerGuideChat;